import connect from '@vkontakte/vkui-connect-promise'
export default {
    namespaced: true,
    actions: {
        fetchUserData(ctx) {
            connect.send('VKWebAppGetUserInfo', {}).then(r => {
                ctx.commit("updateUserData", r.data)
                ctx.commit("updateStatus", "success")
            })
        }
    },
    mutations: {
        updateUserData(state, user) {
            state.user = user
        },
        updateStatus(state, status) {
            state.status = status
        }
    },
    state: {
        user: {},
        status: "",
    },
    getters: {
        getUserData(state) {
            return state.user
        },
        getStatus(state) {
            return state.status
        }
    }
}