<template>
  <div>
    <Group v-if="getStatus == 'success'" title="Основная информация">
      <List>
        <Cell>
          <template v-slot:before>
            <Avatar type="image" :size="50" :src="getUserData.photo_200" />
          </template>
        </Cell>
        <Cell>Ваша фамилия: {{getUserData.last_name}}</Cell>
        <Cell>Ваше имя: {{getUserData.first_name}}</Cell>
        <Cell>Род: {{getUserData.sex | getGenus}}</Cell>
        <Cell v-if="!!getUserData.country.title">В стране {{getUserData.country.title}}</Cell>
        <Cell v-if="!!getUserData.city.title">В городе {{getUserData.city.title}}</Cell>
        <Cell>Часовой пояс: {{getUserData.timezone | getTimeZone}}</Cell>
        <Cell>Дата рождения: {{getUserData.bdate}}</Cell>
      </List>
    </Group>
    <div v-else>
      <PanelHeader>Загрузка...</PanelHeader>
      <b>Подождите минуточку...</b>
      <ScreenSpinner />
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions("personal", ["fetchUserData"])
  },
  computed: {
    ...mapGetters("personal", ["getUserData", "getStatus"])
  },
  filters: {
    getGenus(n) {
      switch (n) {
        case 0:
          return "Инопланитянин!!!";
        case 1:
          return "Женский";
        case 2:
          return "Мужской";
      }
    },
    getTimeZone(n) {
      if (n < 0) {
        return `GMT${n}`;
      } else {
        return `GMT+${n}`;
      }
    }
  },
  mounted() {
    this.fetchUserData();
    console.log(this.getUserData);
  }
};
</script>