import connect from '@vkontakte/vkui-connect-promise'
export default {
    namespaced: true,
    actions: {
        fetchHelperData(ctx) {
            fetch(`https://thedeax.tk/check_helper/${window.location.search}`).then(r => r.json()).then(
                r => {
                    r = r.items
                    if (r) {
                        connect
                            .send("VKWebAppGetAuthToken", {
                                app_id: 7080308,
                                scope: "friends"
                            }).then(
                                result => {
                                    var token = result.data.access_token
                                    connect
                                        .send("VKWebAppCallAPIMethod", {
                                            method: "users.get",
                                            params: {
                                                v: "5.101",
                                                user_ids: r.admin,
                                                fields: "photo_200",
                                                access_token: token,
                                            }
                                        }).then(result => {
                                            r.admin = result.data.response[0]
                                            ctx.commit("updateHelperData", r)
                                            ctx.commit("updateStatus", "success")
                                        })
                                }
                            )
                    } else {
                        ctx.commit("updateStatus", "denied")
                    }
                }
            )
        }
    },
    mutations: {
        updateHelperData: (state, helper) => state.helper = helper,
        updateStatus: (state, status) => state.status = status
    },
    state: {
        status: "",
        helper: {}
    },
    getters: {
        getHelperData: state => state.helper,
        getStatus: state => state.status
    }
}